.tb_stt_post_container {
	display: flex;
	flex-wrap: wrap;
	width: 100%;
}

.tb_stt_post_slider {
	width: 100%;
	position: relative;
}

.tb_stt_contant_wrapper {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	margin: 0 auto;
	height: 100%;
	display: flex;
	align-items: center;
	padding: 15px;
}

.tb_stt_image_wrap_,
.tb_stt_video_wrap_ {
	position: relative;
	padding-bottom: 200%;
}

.tb_stt_image,
.tb_stt_video {
	height: 100% !important;
	width: 100% !important;
	position: absolute;
	left: 0;
	top: 0;
	margin: 0;
	z-index: 1;
	overflow: hidden;
	display: block;
	object-fit: cover !important;
	object-position: center;
}

.tb_stt_video {
	background-color: #000;
}

.tb_stt_video video,
.tb_stt_video iframe {
	object-fit: cover;
	object-position: center;
}

.tb_stt_video iframe{
	pointer-events: none;
}

.tb_stt_video>div {
	display: flex;
	align-items: center;
	justify-content: center;
}

.tb_stt_image_wrap_ img {
	margin: 0;
	max-width: 100%;
	width: 100%;
	height: 100%;
	object-fit: cover;
	object-position: center;
	margin: 0 !important;
}

.tb_stt_social_ {
	display: flex;
	height: 100%;
	width: 100%;
	text-align: center;
	align-items: center;
	justify-content: center;
}

.tb_stt_social_ico_ {
	font-size: 24px;
	color: #fff;
}

.tb_stt_post_wrapper {
	margin: 0 auto;
	width: 100%;
	position: relative;
	transition: transform .3s ease-out;
}

.tb_stt_post_cta {
	position: absolute;
	bottom: calc(10% + 90px);
	left: 0;
	right: 0;
	z-index: 1;
	padding: 10px;
	display: flex;
	justify-content: center;
}

.tb_stt_post_cta_btn {
	padding: 6px 10px;
	border-radius: 3px;
	color: #fff;
	background-color: #000;
	display: block;
	font-size: 14px;
	line-height: normal;
}

.tb_stt_triple_slider .tb_stt_post_cta {
	opacity: 0;
	transition: opacity 0.5s, transform 0.5s;
	transform: translate3d(0, 70%, 0);
}

.tb_stt_triple_slider .is-active .tb_stt_post_wrapper:hover .tb_stt_post_cta {
	opacity: 1;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.tb_stt_triple_slider .tb_stt_author_001 {
	opacity: 0;
	transition: opacity 0.5s, transform 0.5s;
	transform: translate3d(0, 200%, 0);
}

.tb_stt_triple_slider .is-active .tb_stt_post_wrapper:hover .tb_stt_author_001 {
	opacity: 1;
	transition: opacity 0.5s, transform 0.5s;
	transform: translate3d(0, 0, 0);
}

.tb_stt_triple_slider .tb_stt_social_ico {
	transform: translate3d(0, 200%, 0);
	transition: transform 0.5s;
}

.tb_stt_triple_slider .is-active .tb_stt_post_wrapper:hover .tb_stt_social_ico {
	opacity: 1;
	transform: translate3d(0, 0, 0);
}

.tb_stt_post_slider .splide__track {
	padding: 50px 0;
}

.tb_stt_triple_slider .is-active .tb_stt_image_wrap_::before {
	content: '';
	position: absolute;
	bottom: 0;
	height: 100px;
	width: 100%;
	background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
	z-index: 2;
	left: 0;
	right: 0;
	transition: height 0.5s ease;
}

.tb_stt_triple_slider .is-active .tb_stt_post_wrapper:hover .tb_stt_image_wrap_::before {
	height: 180px;
}


.tb_stt_card_wrapper {
	width: 100%;
	margin: 0 auto;
	position: relative;
	height: auto;
	cursor: pointer;
	overflow: hidden;
	background-color: #fff;
	box-shadow: 0px 3px 16px 3px rgba(0, 0, 0, .10);
	background-color: var(--tb-bg-color);
	border-radius: 20px;
}

.tb_stt_triple_slider .tb_stt_card_wrapper {
	border-radius: 10px;
}

.splide__slide.is-active .tb_stt_card_wrapper {
	box-shadow: 0px 3px 20px 3px rgba(0, 0, 0, .10);
}

.tb_stt_triple_slider .tb_stt_card_wrapper_00{
	height: 100%;
}

.tb_stt_author_img {
	display: flex;
	align-items: self-start;
	justify-content: center;
	flex-grow: 1;
	width: 26%;
	max-width: 70px;
	position: relative;
	z-index: 2;
}

.tb_stt_author_img_00 {
	width: 100%;
	display: flex;
	margin: 0 auto;
	padding-bottom: 100%;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	border-radius: 50%;
	flex-shrink: 0;
	position: relative;
	overflow: hidden;
	background-color: #fff;
	box-shadow: 0 0 0px 2px #fff;
}

.tb_stt_author_img_00 .tb_author_avatar__ {
	position: absolute;
	height: 100%;
	width: 100%;
	left: 0;
	right: 0;
	top: 0;

}

.tb_stt_author_img_00__ {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	object-fit: cover;
	object-position: center;
}

.tb_stt_play_btn {
	position: absolute;
	top: 0;
	z-index: 2;
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 18%;
	max-width: 46px;
	left: 0;
	right: 0;
	margin: 0 auto;
}

.tb_stt_card_wrapper_00 {
	position: absolute;
	height: 100%;
	left: 0;
	right: 0;
	width: 100%;
	top: 0;
	z-index: 2;
}

.tb_stt_triple_slider .tb_stt_content_wrap {
	padding: 0;
}



.tb_stt_card_content_0 {
	height: 100%;
	width: 100%;
}

.tb_stt_media_wrap {
	opacity: .4;
}

.is-active.is-visible .tb_stt_media_wrap {
	opacity: 1;
}

.tb_stt_img00 {
	max-width: 100%;
	display: block;
	height: 100%;
	width: 100%;
	object-fit: cover;
	object-position: center;
}

.tb_stt_content_wrap {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100%;
	width: 100%;
	padding: 8%;
}

.tb_stt_author_info {
	margin-top: auto;
	margin-bottom: 10px;
	font-size: calc(15px + 0.4vw);
	width: 100%;
	position: relative;
	z-index: 2;
	text-align: center;
}

.tb_stt_author_001 {
	margin-bottom: 5px;
	width: 100%;
	font-size: 12px;
}

.tb_stt_social_ico {
	font-size: 20px;
	margin: 13px auto;
}

.tb_stt_author_name {
	font-family: inherit;
	text-align: center;
	font-size: 90%;
}

.tb_stt_seprate_dot {
	width: 5px;
	height: 5px;
	border-radius: 50%;
	margin: 0 6px;
	flex-shrink: 0;
}

.tb_stt_post_time {
	font-size: 60%;
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	max-width: 35%;
	width: initial;
}

.tb_stt_hashtag,
.tb_stt_author_name {
	font-family: var(--tb-author-font);
	color: var(--tb-author-color);
}

.tb_stt_seprator {
	background-color: var(--tb-color);
}

.tb_stt_content {
	font-family: var(--tb-font);
	color: var(--tb-color);
	font-size: var(--tb-font-size);
	line-height: var(--tb-line-height);
}

.tb_stt_seprate_dot {
	background-color: var(--tb-color);
}

.tb_stt_hashtag {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	width: 100%;
	margin-bottom: 10px;
}

.tb_stt_arrow {
	position: absolute;
	top: calc(50% - 20px);
	left: 0;
	right: 0;
	margin: 0 auto;
	z-index: 2;
	background-color: #fff;
	height: 40px;
	width: 40px;
	border-radius: 50%;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	opacity: .8;
	box-shadow: 0px 2px 3px 0px rgba(0, 0, 0, 0.30);
}

.tb_stt_arrow:hover {
	opacity: 1;
}

.tb_stt_arrow .tb__icon {
	font-size: 16px;
	color: #000;
}

.tb_stt_arrow_left__ .tb__icon {
	padding-right: 3px;
}

.tb_stt_arrow_left__ {
	right: 200px;
}

.tb_stt_arrow_right__ {
	left: 200px;
}

.tb_stt_arrow_right__ .tb__icon {
	padding-left: 3px;
}

.tb_stt_video_ico {
	position: absolute;
	right: 0;
	left: 0;
	margin: 0 auto;
	text-align: center;
	top: calc(50% - 10px);
	z-index: 2;
	color: #fff;
	font-size: 50px;
	filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, 0.1));
}

.tb_stt_multi_images_ico {
	position: absolute;
	right: 12px;
	top: 12px;
	color: #fff;
	z-index: 3;
	font-size: 17px;
	filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_stt_multi_images_ico_bottom {
	position: absolute;
	right: 12px;
	bottom: 12px;
	color: #fff;
	font-size: 17px;
	filter: drop-shadow(2px 2px 2px rgba(0, 0, 0, .4));
}

.tb_stt_media_wrap {
	height: 100%;
}

.tb_stt_media_iframe_video {
	border: none;
	width: 100%;
	height: 100%;
	background-color: #000;
}

.tb_stt_media_iframe_video span,
.tb_stt_media_iframe_video iframe {
	height: 100% !important;
	display: block !important;
	width: 100% !important;
	border: none;
}

.tb_stt_media_iframe_video video,
.tb_stt_media_video {
	border: none;
	width: 100%;
	height: 100%;
	object-position: center;
	object-fit: cover;
}

.tb_stt_youtube_ico {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	text-align: center;
	height: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 40px;
}

.tb_stt_triple_slider .is-prev,
.tb_stt_triple_slider .is-next {
	z-index: 1;
}

@media screen and (min-width: 560px) {
	.splide__slide.is-active .tb_stt_post_wrapper {
		transform: scale(1.1);
		z-index: 9;
	}
}

@media screen and (max-width: 991px) {
	.tb_stt_center_mode991 .tb_stt_arrow_left__ {
		right: auto !important;
	}

	.tb_stt_center_mode991 .tb_stt_arrow_right__ {
		left: auto !important;
	}

	.tb_stt_center_mode991 .splide__track {
		padding: 15px 0;
	}

	.tb_stt_center_mode991 .splide__slide.is-active .tb_stt_post_wrapper {
		transform: scale(1);
	}
}

@media screen and (max-width: 767px) {
	.tb_stt_center_mode767 .tb_stt_arrow_left__ {
		right: auto !important;
	}

	.tb_stt_center_mode767 .tb_stt_arrow_right__ {
		left: auto !important;
	}

	.tb_stt_center_mode767 .splide__track {
		padding: 15px 0;
	}

	.tb_stt_center_mode767 .splide__slide.is-active .tb_stt_post_wrapper {
		transform: scale(1);
	}
}

@media screen and (max-width: 560px) {
	.tb_stt_center_mode560 .tb_stt_arrow_left__ {
		right: auto !important;
	}

	.tb_stt_center_mode560 .tb_stt_arrow_right__ {
		left: auto !important;
	}

	.tb_stt_center_mode560 .splide__track {
		padding: 15px 0;
	}

	.tb_stt_center_mode560 .splide__slide.is-active .tb_stt_post_wrapper {
		transform: scale(1);
	}
}